import React, { Component } from 'react';
import RetroButton from '../retroButton/RetroButton'

import coin from '../../../../assets/icons/coin.png'
import endScreen from '../../../../assets/pantallas/endScreen.png'


//imagenes
import goimg from '../../../../assets/products/img/go.png'
import jsimg from '../../../../assets/products/img/js.png'
import pythonimg from '../../../../assets/products/img/python.png'
import htmlimg from '../../../../assets/products/img/html.png'
import cimg from '../../../../assets/products/img/c.png'
import javaimg from '../../../../assets/products/img/java.png'
import reactimg from '../../../../assets/products/img/react.png'

//styled
import styled from 'styled-components'

const techs =[ 
    {
        id:"go",
        img:goimg,
        cost:"free",
        level:"hard",

    },
    {
        id:"python",
        img:pythonimg,
        cost:"paid",
        level:"easy",

    },
    {
        id:"js",
        img:jsimg,
        cost:"free",
        level:"medium",

    },
    {
        id:"html",
        img:htmlimg,
        cost:"free",
        level:"easy",

    },
    {
        id:"c",
        img:cimg,
        cost:"paid",
        level:"hard",

    },
    {
        id:"react",
        img:reactimg,
        cost:"paid",
        level:"medium",

    },{
        id:"java",
        img:javaimg,
        cost:"paid",
        level:"easy",

    },
]

class Filter extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            show: <div> { techs.map( product => <> { this.all( product ) } </> ) } </div>,
            Mostrador:<Mostrador className="default">All</Mostrador>,
            easy:true,
            medium:true,
            hard:true,
            paid:true,
            free:true,
         }
    }

    hard = (product , state) => {
        let hard = state.hard
        if (hard) {
            if (product.level==="hard") {
             this.setState({Mostrador:<Mostrador className="hard">Hard</Mostrador>})
                return (<Product src ={ product.img } />)
         }            
    }  else {
        this.setState(current => ({hard:!current.hard}))
        if (product.level==="hard") {
            this.setState({Mostrador:<Mostrador className="hard">Hard</Mostrador>})
            return (<Product src ={ product.img } />)
        } 

    }

    }

    medium = (product , state) => {
        
        var free = state.free
        var paid = state.paid
        let medium = state.medium
        if (medium) {
            if (product.level==="medium") {
                this.setState({Mostrador:<Mostrador className="medium">Medium</Mostrador>})
                return (<Product src ={ product.img } />)
            }            
        }  else {
            this.setState(current => ({medium:!current.medium}))
            if (product.level==="medium") {
                this.setState({Mostrador:<Mostrador className="medium">Medium</Mostrador>})
                return (<Product src ={ product.img } />)
            } 

        }

    }

    easy = (product , state) => {
        
        var free = state.free
        var paid = state.paid
        var easy = state.easy
        var medium = state.medium
        var hard = state.hard
        if (easy) {

            if (product.level==="easy") {
                this.setState({Mostrador:<Mostrador className="easy">Easy</Mostrador>})
                return (<Product src ={ product.img } />)
             
            }            
        } 
        else {
            this.setState(current => ({easy:!current.easy}))
            if (product.level==="easy") {
                this.setState({Mostrador:<Mostrador className="easy">Easy</Mostrador>})
                return (<Product src ={ product.img } />)
            } 

        }

    }

    free = (product , state) => {

        let free    = state.free
        let easy    = state.easy
        let medium  = state.medium
        let hard    = state.hard

        if (free) {
            if (easy) {
                if ( product.level==="easy"&& product.cost==="free") {
                    this.setState({Mostrador:<Mostrador > easy <Mostrador className="gold"> / free</Mostrador></Mostrador>})
                    return (<Product src ={ product.img } />)
                }
    
            } else {
                if (medium) {
                    if ( product.level==="medium"&& product.cost==="free") {
                        this.setState({Mostrador:<Mostrador> medium <Mostrador className="gold"> / free</Mostrador></Mostrador>})
                        return (<Product src ={ product.img } />)
                    }
                } else {
                    if(hard){
                        if ( product.level==="hard"&& product.cost==="free") {
                            this.setState({Mostrador:<Mostrador>hard <Mostrador className="gold"> / free</Mostrador></Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
    
                    } else {
                        if (product.cost==="free") {
                            this.setState({Mostrador:<Mostrador><Mostrador className="gold"> free</Mostrador></Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
                        
                    }
                   
    
                }
    
            }
        
       
        } else {
            this.setState(current => ({free:!current.free}))
            if (easy) {
                if ( product.level==="easy"&& product.cost==="free") {
                    this.setState({Mostrador:<Mostrador> easy <Mostrador className="gold"> / free</Mostrador></Mostrador>})
                    return (<Product src ={ product.img } />)
                }
    
            } else {
                if (medium) {
                    if ( product.level==="medium"&& product.cost==="free") {
                        this.setState({Mostrador:<Mostrador>medium <Mostrador className="gold"> / free</Mostrador></Mostrador>})
                        return (<Product src ={ product.img } />)
                    }
                } else {
                    if(hard){
                        if ( product.level==="hard"&& product.cost==="free") {
                            this.setState({Mostrador:<Mostrador>hard <Mostrador className="gold"> / free</Mostrador></Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
    
                    } else {
                        if (product.cost==="free") {
                            this.setState({Mostrador:<Mostrador><Mostrador className="gold"> free</Mostrador></Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
                        
                    }
                   
    
                }
    
            }
        }
        
      
     
    }

    paid = (product , state) => {
        let paid = state.paid
        let easy = state.easy
        let medium = state.medium
        let hard = state.hard

        
            if (easy) {
                if ( product.level==="easy"&& product.cost==="paid") {
                    this.setState({Mostrador:<Mostrador> easy / paid</Mostrador>})
                    return (<Product src ={ product.img } />)
                }
    
            } else {
                if (medium) {
                    if ( product.level==="medium"&& product.cost==="paid") {
                        this.setState({Mostrador:<Mostrador> medium / paid</Mostrador>})
                        return (<Product src ={ product.img } />)
                    }
                } else {
                    if(hard){
                        if ( product.level==="hard"&& product.cost==="paid") {
                            this.setState({Mostrador:<Mostrador> hard / paid</Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
    
                    } else {
                        if (product.cost==="paid") {
                            this.setState({Mostrador:<Mostrador>paid</Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
                        
                    }
                   
                }
    
            }
        
    }

    all = (product) => {
        
        return (<Product src ={ product.img } />)
        
    }

    show = ( type ) =>{
        
        switch ( type ) {
            
            case "all":
                this.setState({show:<div>{ techs.map( aux => <> { this.all( aux ) } </> ) } </div>} )
                this.setState({Mostrador:<Mostrador>All</Mostrador>})
                this.setState({easy:false})
                this.setState({medium:false})
                this.setState({hard:false})
                this.setState({free:false})
                this.setState({paid:false})
                break;

            case "easy":
                this.setState({show:<div>{ techs.map( aux => <> { this.easy( aux , this.state) } </> ) } </div>} )
                this.setState({easy:true})
                this.setState({medium:false})
                this.setState({hard:false})
                break;

            case "medium":
                this.setState({show:<div>{ techs.map( aux => <> { this.medium( aux , this.state) } </> ) } </div>} )
                this.setState({easy:false})
                this.setState({medium:true})
                this.setState({hard:false})
                break;
            case "hard":
                this.setState({show:<div>{ techs.map( aux => <> { this.hard( aux , this.state) } </> ) } </div>} )
                this.setState({easy:false})
                this.setState({medium:false})
                this.setState({hard:true})
                break;
            
            case "free":
                this.setState({show:<div>{ techs.map( aux => <> { this.free( aux , this.state) } </> ) } </div>} )
                this.setState({free:true})
                this.setState({paid:false})
                break;

            case "paid":
                this.setState({show:<div>{ techs.map( aux => <> { this.paid( aux , this.state) } </> ) } </div>} )
                this.setState({free:false})
                this.setState({paid:true})
                break;
        
            default:
                break;
        }
        
           
    }

    wardEasy   = () =>{ this.show("easy")  }
    wardMedium = () =>{ this.show("medium")}
    wardHard   = () =>{ this.show("hard")  }
    wardFree   = () =>{ this.show("free")  }
    wardPaid   = () =>{ this.show("paid")  }
    wardAll    = () =>{ this.show("all")   }

    render() { 
        const buttons = {

            easy:   <Button onClick     = {this.wardEasy}   > <RetroButton text="Easy"/>   </Button>,

            medium: <Button onClick     = {this.wardMedium} > <RetroButton text="Medium"/> </Button>,
 
            hard:   <Button onClick     = {this.wardHard}   > <RetroButton text="Hard"/>   </Button>,
 
            free:   <ButtonCost onClick = {this.wardFree}   > <RetroButton text="Free"/>   </ButtonCost>,

            paid:   <ButtonCost onClick = {this.wardPaid}   > <RetroButton text="Paid"/>   </ButtonCost>,

            all:    <All onClick     = {this.wardAll}    > <RetroButton text="All"/>  </All>,

        }

        return ( 

            <Page>
                 <Title> All Courses </Title>
                 <Subtitle> Filter By  <Coin src={coin} /> </Subtitle>
                 <End src={endScreen}/>

                 <Level>    
                            {buttons.easy}
                            {buttons.medium}
                            {buttons.hard} 
                </Level>
                <DivMostrador>
                    {buttons.all}
                    {this.state.Mostrador}
                    
                </DivMostrador>
                <DivProducts>
                            {this.state.show}
                </DivProducts>

            </Page>

         );

    }

}
 
export default Filter;



//estilos
const Page = styled.div`


`;
const Title = styled.h1`
  padding: 20px 0 20px 0;
  font-size: 50px;
  text-align: center;
  text-shadow: 5px -1px #66658d;
  color: #908fbb;
  background-color: #262138;
  font-family: "Press Start 2P";
`;
const Subtitle = styled.h2`
 padding: 10px 0 25px 0;
  color: #66658d;
  margin: 0px;
  text-align: center;
  font-size: 15px;
  font-family: "Press Start 2P";
  background-color: #262138;
`;
const Coin =styled.img`
 width:30px;
 margin-bottom: -7px;
`;
const End = styled.img`
 width: 100%;
 @media (min-width: 768px) {
    width: 50%;
  }
`;
const Level = styled.div`
padding: 15px 0 0px 0;
background-color: #181818;
margin-top: -10px;
width: 100%;
box-sizing: border-box;
 @media (min-width: 768px) {
    width: 50%;
  }
`;
const All = styled.div`
margin-top: -10px;
background-color: #181818;
padding: 0px 27px 20px 0;
width:100px
float: right;
height:50px;

`;
const DivProducts = styled.div`
text-align: center;
background-color: #181818;
width: 100%;
height:400px;
display:inline-block;
margin: 0 0 0 0;
position: relative;
padding: 40px 0 0 0;

.gold{
    color: #ffc500;
    font-size:20px ;
}
@media (min-width: 768px) {
    width: 50%;
    
  }
`;
const Button = styled.div`
width: 33.33%;
height: auto;
padding: 20px 0 20px 0;
display: inline-block;
margin-left: auto;
margin-right: auto;
`;
const Product = styled.img`
width:90px;
margin:10px;
border-radius: 100%;

`;
const DivMostrador = styled.div`
width:100%;
height:auto;
text-align: center;
.easy{
    background-color: #66658d;
    
}
.medium{
    background-color: #4ea6a6;
}
.hard{
    background-color: #da6964;
}
.default{
    background-color: #262138;
    text-shadow: 2px -1px #66658d;
  color: #908fbb;
}

`;
const Mostrador = styled.h2`
font-family: "Press Start 2P";
color:#eaeaea;
text-shadow: 5px -1px #000;
font-size: 20px;
letter-spacing: 3px;
padding: 15px 0 13px 0 ;
background-color: #e9b962;

.free{
    color:#00CC29;
}
`;
const ButtonCost = styled.div`
width: 33.33%;
height: auto;
padding: 20px 0 20px 0;
display: inline-block;
margin-left: auto;
margin-right: auto;
`;